@use 'global' as *;

.sub-mv {
  position: relative;
  height: rem(460);
  @include mq('md') {
    height: rem(548);
  }
}

.sub-mv img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.sub-mv__inner {
  height: inherit;
}

.sub-mv__header {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  @include mq('md') {
    top: 59%;
  }
}

.sub-mv__title {
  font-size: rem(32);
  font-weight: $normal;
  font-family: $second-font-family;
  line-height: 1.2;
  text-align: center;
  color: $base;
  white-space: nowrap;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  opacity: 0;
  @include mq('md') {
    font-size: rem(60);
    line-height: 1.25;
  }
}

.sub-mv__title--page-terms-of-service {
  font-size: rem(38);
  @include mq('md') {
    font-size: rem(75);
  }
}

.char {
  transform: translateY(-100px);
  transition: transform 0.5s;
}
